.test-page {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
  padding-top: 100px;
}

.test-page h1 {
  color: #ff6b35;
  text-align: center;
  margin-bottom: 2rem;
}

.test-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  margin-bottom: 2rem;
}

.test-section h2 {
  color: #ff6b35;
  margin-bottom: 1rem;
}

.test-buttons {
  display: flex;
  gap: 1rem;
  flex-wrap: wrap;
}

.test-btn {
  background: #ff6b35;
  color: white;
  border: none;
  padding: 0.8rem 1.5rem;
  border-radius: 8px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
}

.test-btn:hover {
  background: #ff8c42;
  transform: translateY(-2px);
}

.data-section {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  margin-bottom: 2rem;
}

.data-column {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
}

.data-column h3 {
  color: #ff6b35;
  margin-bottom: 1rem;
  border-bottom: 2px solid #ff6b35;
  padding-bottom: 0.5rem;
}

.data-list {
  max-height: 400px;
  overflow-y: auto;
}

.data-item {
  background: #f8f9fa;
  padding: 1rem;
  margin-bottom: 0.5rem;
  border-radius: 8px;
  border-left: 4px solid #ff6b35;
}

.data-item strong {
  color: #ff6b35;
}

.data-item small {
  color: #666;
}

.navigation-links {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  text-align: center;
}

.navigation-links h3 {
  color: #ff6b35;
  margin-bottom: 1rem;
}

.navigation-links a {
  display: inline-block;
  margin: 0.5rem;
  padding: 0.5rem 1rem;
  background: #ff6b35;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  transition: all 0.3s ease;
}

.navigation-links a:hover {
  background: #ff8c42;
  transform: translateY(-2px);
}

@media (max-width: 768px) {
  .data-section {
    grid-template-columns: 1fr;
  }
  
  .test-buttons {
    flex-direction: column;
  }
} 