.book-now-page {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8f9fa 0%, #e9ecef 100%);
  padding-top: 80px;
}

.booking-hero {
  background: linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url('https://images.unsplash.com/photo-1565299624946-b28f40a0ca4b?w=1920&h=400&fit=crop&crop=center');
  background-size: cover;
  background-position: center;
  color: white;
  text-align: center;
  padding: 4rem 2rem;
}

.booking-hero h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  text-shadow: 2px 2px 4px rgba(0,0,0,0.7);
}

.booking-hero p {
  font-size: 1.2rem;
  max-width: 600px;
  margin: 0 auto;
}

.booking-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 4rem 2rem;
}

.region-selection {
  margin-bottom: 3rem;
}

.region-selection h2 {
  color: #ff6b35;
  margin-bottom: 2rem;
  font-size: 2rem;
  text-align: center;
}

.regions-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 2rem;
}

.region-card {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 5px 15px rgba(0,0,0,0.1);
  cursor: pointer;
  transition: all 0.3s ease;
  border: 3px solid transparent;
}

.region-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 30px rgba(0,0,0,0.2);
}

.region-card.selected {
  border-color: #ff6b35;
  background: linear-gradient(135deg, #fff 0%, #fff8f5 100%);
}

.region-card h3 {
  color: #ff6b35;
  margin-bottom: 1rem;
  font-size: 1.5rem;
}

.region-card p {
  color: #666;
  line-height: 1.6;
  margin: 0;
}

.booking-form-section {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
  margin-bottom: 3rem;
}

.booking-form-section h2 {
  color: #ff6b35;
  margin-bottom: 2rem;
  font-size: 1.8rem;
}

.submit-message {
  padding: 1rem;
  border-radius: 8px;
  margin-bottom: 1.5rem;
  font-weight: bold;
}

.submit-message.success {
  background: #d4edda;
  color: #155724;
  border: 1px solid #c3e6cb;
}

.submit-message.error {
  background: #f8d7da;
  color: #721c24;
  border: 1px solid #f5c6cb;
}

.booking-form {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-group label {
  font-weight: bold;
  color: #333;
}

.form-group input,
.form-group select,
.form-group textarea {
  padding: 0.8rem;
  border: 2px solid #e9ecef;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.3s ease;
}

.form-group input:focus,
.form-group select:focus,
.form-group textarea:focus {
  outline: none;
  border-color: #ff6b35;
}

.form-group input:disabled,
.form-group select:disabled,
.form-group textarea:disabled {
  background: #f8f9fa;
  cursor: not-allowed;
}

.form-row {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1rem;
}

.submit-btn {
  background: linear-gradient(135deg, #ff6b35 0%, #ff8c42 100%);
  color: white;
  border: none;
  padding: 1rem 2rem;
  border-radius: 8px;
  font-size: 1.1rem;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-top: 1rem;
}

.submit-btn:hover:not(:disabled) {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 53, 0.4);
}

.submit-btn:disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
  box-shadow: none;
}

.booking-info {
  background: white;
  padding: 2rem;
  border-radius: 15px;
  box-shadow: 0 10px 30px rgba(0,0,0,0.1);
}

.booking-info h3 {
  color: #ff6b35;
  margin-bottom: 2rem;
  font-size: 1.8rem;
  text-align: center;
}

.info-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  margin-bottom: 2rem;
}

.info-card {
  background: #f8f9fa;
  padding: 1.5rem;
  border-radius: 10px;
  border-left: 4px solid #ff6b35;
}

.info-card h4 {
  color: #ff6b35;
  margin-bottom: 0.5rem;
  font-size: 1.1rem;
}

.info-card p {
  color: #666;
  margin: 0;
  line-height: 1.5;
}

.contact-info {
  border-top: 2px solid #e9ecef;
  padding-top: 1.5rem;
  text-align: center;
}

.contact-info h3 {
  color: #ff6b35;
  margin-bottom: 1rem;
  font-size: 1.3rem;
}

.contact-info p {
  margin: 0.5rem 0;
  color: #333;
}

.contact-info strong {
  color: #ff6b35;
}

/* Responsive Design */
@media (max-width: 768px) {
  .booking-container {
    padding: 2rem 1rem;
  }
  
  .regions-grid {
    grid-template-columns: 1fr;
  }
  
  .form-row {
    grid-template-columns: 1fr;
  }
  
  .info-cards {
    grid-template-columns: 1fr;
  }
  
  .booking-hero h1 {
    font-size: 2rem;
  }
  
  .booking-hero p {
    font-size: 1rem;
  }
} 